<template>
  <div id='app' class="hello">




     <div class="container">
       <div class="row padding-block">

          <div class="col-lg-4 offset-lg-1">

           <autocomplete :search="search"
           placeholder="Pesquise por crypto moedas"
           aria-label="Pesquise por crypto moedas"          
           :get-result-value="getResultValue" 
            @submit="handleSubmit"
            auto-select />

          </div>          

           <div class="col-lg-2">
                 <button  @click="getCurrencyValues" class="btn btn-circle btn-xl btn-outline-success">
                        <i class="fab fa-btc"></i>  
                 </button>
              
           </div>

          <div class="col-lg-4">
       
          <autocomplete :search="search2"
            placeholder="Pesquise por moedas"
            aria-label="Pesquise por moedas"           
           :get-result-value="getResultValue2" 
            @submit="handleSubmit2"
            auto-select />

           </div>

          </div>

          <div class="row padding-block">
              <div class="col-lg-8 offset-lg-2">
                  <div v-if="resultCoin" class="table-responsive">

              <div class="form-inline padding-form" >
                 <input type='number' class="form-control" v-model="c"  min='0' />
                 <button class="btn btn-outline-success" @click="calculate" >Calcular valor em {{resultCoin.data.comparedTo._symbol}}</button> 
              </div>


                     
                    
                     <table class="table table-striped table-hover">
                        <tr>
                           <td>{{ this.compareCoin.name}}</td>
                           <td>{{resultCoin.data.cryptoCoin.qty}}</td>
                        </tr>    
                       <tr>
                          <td>{{resultCoin.data.base._symbol}}</td>
                          <td>{{resultCoin.data.base._value | formatCrrency}}</td>
                       </tr>
                       <tr>   
                          <td>{{resultCoin.data.comparedTo._symbol}}</td>
                          <td>{{resultCoin.data.comparedTo._value}}</td>                                   
                       </tr>
                       <tr>
                          <td>Data: </td>
                          <td>{{resultCoin.data.date | formatDate}}</td>  
                       </tr>
                     </table> 
                 </div>
              </div>
          </div>

       </div> 


  </div>
</template>

<script>

import Autocomplete from '@trevoreyre/autocomplete-vue'
import { asyncLoading } from 'vuejs-loading-plugin'

export default {
  name: 'CryptoCoinCompareComponent',
  props: {
    msg: String,
    crypto: String
  },
  data:()=>({
    cryptoCoins: [],
    resultCoin: null,
    compareCoin: null,
    coins: null,
    coin1: null,
    c: null
  }),
  components: {
    Autocomplete
  },
  methods:{
    loadData(config)
    {      
        axios(config)
         .then(res => {
             this.cryptoCoins = res.data.data
           }).catch(err => {
             console.log(err)
        })
            
    },
    loadCurrency(config)
    {      
        axios(config)
         .then(res => {
             this.coins = res.data.rates
           }).catch(err => console.log(err))
            
    },
    loadCoin(config)
    {      
       
       let result =  axios(config)
         .then(res =>  res.data)
            .catch(err => console.log(err))
          this.$loading(true)
           asyncLoading(result).then((res)=>{
                this.resultCoin =  res
                this.msg = res.name
           }).catch((err)=>{
             console.log(err)
           })
            
    },
    loadResult(config)
    {      
       
       let result =  axios(config)
         .then(res =>  res.data)
            .catch(err => console.log(err))
          this.$loading(true)
           asyncLoading(result).then((res)=>{
                this.resultCoin =  res
                this.msg = res.name
           }).catch((err)=>{
             console.log(err)
           })
            
    },
    search(input) {
      if (input.length < 1) { return [] }
      return this.cryptoCoins.filter(el => {
        return el.website_slug.toLowerCase()
          .startsWith(input.toLowerCase())
      })
    },
    search2(input) {
      if (input.length < 1) { return [] }
      return Object.keys(this.coins).filter((el, v) => {
        return el.toLowerCase()
          .startsWith(input.toLowerCase())
      })
    },    
    getResultValue(result) {
      return result.name
    },    
    handleSubmit(result) {  
     this.compareCoin= result
      if(this.compareCoin != null &&  this.coin1 != null)
      {
          this.loadResult({url: `/crypto/${this.compareCoin.id}/${this.coin1}/1`})
      }

    },
    getResultValue2(result) {
      return result
    },    
    handleSubmit2(result) {  
      this.coin1= result
      if(this.compareCoin != null &&  this.coin1 != null)
      {
          this.loadResult({url: `/crypto/${this.compareCoin.id}/${this.coin1}/1`})
      }
    },
    getResult(){
      return this.resultCoin? 
       this.resultCoin.data.comparedTo._symbol : 'USD'
    },  
    calculate(){
        this.loadResult({url: `/crypto/${this.compareCoin.id}/${this.getResult()}/${this.c}`})
    },
    getCurrencyValues(){

    }
  },
  beforeMount(){
    
  },
  created() {
      this.loadData({url: '/crypto'})
      this.loadCurrency({url: '/currency'})
  }
}
</script>